@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+KR&display=swap);
.Header{
    height: 100px;
    width: 100%;

    margin-bottom: 20px;
    padding-left: 15px;
    padding-right: 15px;

    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    background-color: #0A154E;
    position: fixed;
    top: 0;
}

.Header a{
    text-decoration: none;
}

.headerNav{
    height: 90px;

    align-items: center;
    display: flex;
    flex-direction: row;
}

.headerNavItem{
    height: 90px;
    width: 220px;

    align-items: center;
    display: flex;
    flex-direction: column;

    background-color: #0A154E;
    transition: background-color 0.3s ease-in-out;
}

.headerNavItem:hover{
    background-color: #F5D040;
}

.headerNavItemTitle{
    height: 85px;
    width: 100%;

    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;

    background-color: #0A154E;
}

.headerNavItemTitle p{
    margin: 0;

    color: #F5D040;
    font-family: 'IBM Plex Sans KR', sans-serif;
    font-size: 25px;
}

.headerTitle{
    height: 60px;

    align-items: center;
    display: flex;
    flex-direction: row;
}

.headerTitleText{
    height: 60px;

    margin-left: 15px;

    align-items: center;
}

.headerTitleText p{
    height: 60px;

    margin: 0;

    color: #F5D040;
    font-family: 'Righteous', cursive;
    font-size: 50px;
}
#SectionContainer{
    width: 100%;

    display: flex;
    flex-direction: column;

    align-items: center;
    justify-content: center;

    margin-top: 150px;
}

#SectionWrapper{
    height: 100%;
    width: 1300px;
}
html, body{
    height: 100%;
    overflow-x: hidden;
}

body{
    margin: 0px;
}

#Container{
    width: 100%;
}
.divHome{
    width: 100%;
}
.divBoardItem{
    width: 100%;

    display: flex;
    flex-direction: row;
}

.divBoardItemDivider{
    height: 1px;
    width: 100%;

    background-color: #BBBBBB;
}

.pBoardItemAuthor{
    width: 200px;

    margin: 0;
}

.pBoardItemDate{
    width: 200px;

    margin: 0;
}

.pBoardItemTitle{
    width: 750px;

    margin: 0 0 0 50px;
}
#divBoard{
    width: 100%;
}

#divBoardContainer{
    height: 100%;
    width: 1200px;

    margin: auto;
}

#divBoardItemContainer{
    width: 100%;
}

#divBoardTitle{
    width: 100%;
}

#divBoardTitleDivider{
    height: 3px;
    width: 100%;

    background-color: #0a154e;
}

#divBoardTitleText{
    width: 100%;

    display: flex;
    flex-direction: row;
}

.pBoardPostAuthor{
    width: 200px;

    margin: 0;
}

.pBoardPostDate{
    width: 200px;

    margin: 0;
}

.pBoardPostTitle{
    width: 750px;

    margin: 0 0 0 50px;
}
#divIntroduction{
    width: 100%;
}
#divMember{
    width: 100%;
}

#divProject{
    width: 100%;
}

